<template>
  <div class="Chart">
    <header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>图表</span></header>
    <div class="container">

       <div id="main" style="width: 500px;height:400px;"></div>
       <!--  -->

    </div>
  </div>
</template>
<script type="text/javascript">
  
  import echarts from "echarts"

  export default{
    name:"Chart",
    data(){
      return{ 
      }
    },
    mounted(){

        var neData = ["奥丁格项目", "深圳怡宝KA", "怡宝饮料", "百翼白酒公司", "上海屈臣氏项目", "演示版","广州怡宝KA"];
        var neValue = [8044.5, 2728.059, 1829.445, 6.58, 0, 249.257,18.706];
        var data = [];

        for(var i = 0;i< neData.length ; i++){
            data[i]={
            name:"",
            value:""
          };

          data[i].name = neData[i];
          data[i].value = neValue[i];
        }

        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById('main'));

        // 指定图表的配置项和数据
        var option = {
            title: {
                text: ''
            },
            //提示框组件,鼠标移动上去显示的提示内容
            tooltip: {
               trigger: 'item',
               formatter: "{a} <br/>{b}: {c} ({d}%)"//模板变量有 {a}、{b}、{c}、{d}，分别表示系列名，数据名，数据值，百分比。
            },
            //图例
            legend: {
                //图例垂直排列
                orient: 'horizontal',
                y: 'bottom',  
                bottom:200,
                //data中的名字要与series-data中的列名对应，方可点击操控
                data:["奥丁格项目", "深圳怡宝KA", "怡宝饮料", "百翼白酒公司", "上海屈臣氏项目", "演示版","广州怡宝KA"]
            },
            series: [{
              name: '体积',
              type: 'pie',
              data: data,
              radius : ["26%", '45%'],
              center: ['60%', '50%'],
              markPoint:{
                symbol:"triangle"
              },
              label: {
                normal: {
                  show: true, 
                  position: 'right',
                  formatter: "{b} : {d}%",//模板变量有 {a}、{b}、{c}、{d}，分别表示系列名，数据名，数据值，百分比。{d}数据会根据value值计算百分比

                  textStyle : {                   
                    align : 'center',
                    baseline : 'middle',
                    fontFamily : '微软雅黑',
                    fontSize : 15,
                    fontWeight : 'bolder'
                  }
                },
              },

            }]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);  
    },
    created () {
    }
  }
</script>
<style scoped lang="less">
  .Chart{
    .container{
      width: 100%;
      div{
         margin-top: 180/50rem;
         canvas{
         }
      }
     
    }
  }
</style>


