<template>
	<div class="rf_class_OrderDetailQuery">
		<header><i class="iconfont icon-xiangzuo1" @click="toRFOrderShip"></i><span>发运-查看明细</span></header>
		<div class="container">

			<table cellspacing="0">
				<thead>
					<tr>
						<td  @click="SortArr('RFOrderShip_RFOrderDetailQuery','Sku','SortCondition1',$event)">商品代码</td>
						<td  @click="SortArr('RFOrderShip_RFOrderDetailQuery','qty','SortCondition2',$event)">数量</td>
						<td  @click="SortArr('RFOrderShip_RFOrderDetailQuery','QtyCase','SortCondition3',$event)">箱数</td>
						<td  @click="SortArr('RFOrderShip_RFOrderDetailQuery','Descr','SortCondition4',$event)">商品名称</td>
						<td  @click="SortArr('RFOrderShip_RFOrderDetailQuery','Susr2','SortCondition5',$event)">单位</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in RFOrderShip_RFOrderDetailQuery'  :id="index"  :key='index'   @click="selectData(index)">
						<td>{{dataItem.Sku}}</td>
						<td>{{dataItem.qty}}</td>
						<td>{{dataItem.QtyCase}}</td>
						<td>{{dataItem.Descr}}</td>
						<td>{{dataItem.Susr2}}</td>
					</tr>
				</tbody>
			</table>


			<div class="bottomNum">
				<div>
					<span>总数量</span>
					<input v-model="QtyTotal" readonly></input>
				</div>
				<div>
					<span>商品数</span>
					<input v-model="SkuCnt" readonly></input>
				</div>
			</div>

		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"RFOrderDetailQuery",
		data(){
			return{

				RFOrderShip_RFOrderDetailQuery:[],// 列表数据
				QtyTotal:0, // 总数量
				SkuCnt:0,   // 商品数
				SortCondition1:true,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
			}
		},
		mounted(){
			$("thead").find("td").append('<i class="iconfont"></i>')
		},
		created(){


			// 获取从查询页面传来的数据
			this.QtyTotal = this.$store.state.RFOrderShip_RFOrderQuery.QtyTotal;
			this.SkuCnt = this.$store.state.RFOrderShip_RFOrderQuery.SkuCnt;

			this.RFOrderShip_RFOrderDetailQuery = this.$store.state.RFOrderShip_RFOrderDetailQuery;
		},
		methods:{
			// 返回上一页 发运页面
			toRFOrderShip:function(){

				this.$router.push({
					name:"RFOrderShip",
					query:{
						OrderKey:this.$route.query.OrderKey,
						RequestedShipDate:this.$route.query.RequestedShipDate,
						C_company:this.$route.query.C_company,
						C_address1:this.$route.query.C_address1,
						QtyTotal:this.$route.query.QtyTotal,
						TotalVol:this.$route.query.TotalVol,
						TotalWgt:this.$route.query.TotalWgt,
					}
				})
			},
		}
	}
</script>
<style lang="less" scoped>
.rf_class_OrderDetailQuery{
	&>.container{
		width:100%;
		table{
			tr{
				td:nth-child(1){
					min-width:160/50rem;
				}
				td:nth-child(4){
					min-width:320/50rem;
				}
			}
		}

		&>div{
			overflow: hidden;
			width:970/50rem;
			height: 80/50rem;
			line-height: 80/50rem;
			border-bottom: 1/50rem solid #999;
			text-align: center;
			&>span{
				float: left;
				height:100%;
				width:80/50rem;
				text-align:center;
			}
		}
	}

	.bottomNum{
		width: 100%;
		height: 80/50rem;
		line-height: 80/50rem;
		position: fixed;
		left: 0;
		bottom: 0;
		background-color: #fff;
		z-index: 5;
		&>div{
			float: left;
			width: 375/50rem;
			height: 60/50rem;
			span{
				float:left;
				height: 60/50rem;
				margin-left: 75/50rem;
				margin-right: 10/50rem;
			}
			input{
				width: 150/50rem;
				text-align: center;
				height: 40/50rem;
				font-size: 29/50rem;
				background-color:#f0f0f0;
				outline: none;
			}
		}

	}
}

@media screen and (max-width: 332px) and (min-width: 251px){
	.rf_class_OrderDetailQuery{
		&>.container{
			table{
				tr{
					td:nth-child(1){
						min-width:200/50rem;
					}
					td:nth-child(4){
						min-width:320/50rem;
					}
				}
			}
		}
	}
}
@media screen and (max-width: 250px){
	.rf_class_OrderDetailQuery{
		&>.container{
			table{
				tr{
					td:nth-child(1){
						min-width:260/50rem;
					}
					td:nth-child(4){
						min-width:400/50rem;
					}
				}
			}
		}

		.bottomNum{
		 &>div{
		    	span{
				    margin-left: 35/50rem;
				}
				input{
					width: 140/50rem;
				}
			}
		}

	}
}
</style>


