<template>
	<div class="TakeDelivery">
		<header><i class="iconfont icon-xiangzuo1" @click="ToReceipt"></i><span>收货</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<div>
					<span>收货单号</span>
					<input    class="readonly_input" readonly  v-model="input_ReceiptNum"></input>
				</div>
				<div>
					<span>客户单号</span>
					<input    class="readonly_input"  readonly  v-model="input_CustomerNum"></input>
				</div>
			</div>
			<div>
				<div>
					<span>商品代码</span>
					<input  class="readonly_input"  readonly    v-model="input_CommodityCode"></input>
				</div>
				<div>
					<span>客户代码</span>
					<input    class="readonly_input"  readonly  v-model="input_CustomerCode"></input>
				</div>
			</div>
			<div class="goodsName">
				<span>商品名称</span>
				<textarea  class="readonly_input" v-model="input_CommodityName"   readonly></textarea>
			</div>
			<div>
				<div>
					<span>待收数量</span>
					<input    class="readonly_input"  readonly  v-model="input_toBeReceived"></input>
				</div>
				<div>
					<span>已收数量</span>
					<input    class="readonly_input"   readonly  v-model="input_AmountReceived"></input>
				</div>
			</div>
			<div>
				<div>
					<!-- @keyup.enter="getFocus('Lottable03OnRFReceiptMandatory')" -->
					<span>数 &nbsp; &nbsp;量<b>*</b></span>
					<input type="number"   requireds="input_num"   @change="toUppercase('input_num')"  @blur="onblur(true,'input_num')"   ref="input_num" @keyup.enter="nextFocus('input_num')"   @focus="g_focus('input_num')"  placeholder="必填"   v-model="input_num"></input>
					<i v-if="input_num" @click="clearInput('input_num')"  class="iconfont icon-cuowu"></i>
				</div>
				<div>
					<span>单位</span>
					<input    class="readonly_input"  readonly  v-model="input_unit"></input>
				</div>
			</div>
			<div class="select"  v-if="parseInt(SummaryData.ShowLottable02OnRFReceipt)">
				<span>货物状态</span>

				<select  ref="input_StateOfGoods" value = "">
					<option  v-for='(dataItem,index) in StateOfGoodsData'  :id="index"  :key='index' >{{dataItem.Descr}}</option>
				</select>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable03OnRFReceipt)" >
				<span>生产批号<b v-if="parseInt(SummaryData.Lottable03OnRFReceiptMandatory)">*</b></span>
				<input ref="Lottable03OnRFReceiptMandatory"  copyRef="Lottable03OnRFReceiptMandatory"  requireds="Lottable03OnRFReceiptMandatory"  @blur="onblur(parseInt(SummaryData.Lottable03OnRFReceiptMandatory),'Lottable03OnRFReceiptMandatory')"  @keyup.enter="nextFocus('Lottable03OnRFReceiptMandatory')"     style="text-transform:none;"   @focus="g_focus('Lottable03OnRFReceiptMandatory')"  v-model="Lottable03OnRFReceiptMandatory"></input>
				<i v-if="Lottable03OnRFReceiptMandatory" @click="clearInput('Lottable03OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable04OnRFReceipt)">
				<span>生产日期<b v-if="parseInt(SummaryData.Lottable04OnRFReceiptMandatory)">*</b></span>
				<input   ref="Lottable04OnRFReceiptMandatory"    @change="toUppercase('Lottable04OnRFReceiptMandatory')"  copyRef="Lottable04OnRFReceiptMandatory"  requireds="Lottable04OnRFReceiptMandatory" @blur="onblur(parseInt(SummaryData.Lottable04OnRFReceiptMandatory),'Lottable04OnRFReceiptMandatory')"   @keyup.enter="nextFocus('Lottable04OnRFReceiptMandatory')"  @focus="g_focus('Lottable04OnRFReceiptMandatory')"     v-model="Lottable04OnRFReceiptMandatory"></input>
				<i v-if="Lottable04OnRFReceiptMandatory" @click="clearInput('Lottable04OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div  v-if="parseInt(SummaryData.ShowLottable05OnRFReceipt)">
				<span>到期日期<b v-if="parseInt(SummaryData.Lottable05OnRFReceiptMandatory)">*</b></span>
				<input   ref="Lottable05OnRFReceiptMandatory"    @change="toUppercase('Lottable05OnRFReceiptMandatory')"   copyRef="Lottable05OnRFReceiptMandatory"   requireds="Lottable05OnRFReceiptMandatory" @blur="onblur(parseInt(SummaryData.Lottable05OnRFReceiptMandatory),'Lottable05OnRFReceiptMandatory')"  @keyup.enter="nextFocus('Lottable05OnRFReceiptMandatory')"   @focus="g_focus('Lottable05OnRFReceiptMandatory')"  v-model="Lottable05OnRFReceiptMandatory"></input>
				<i v-if="Lottable05OnRFReceiptMandatory" @click="clearInput('Lottable05OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div  v-if="parseInt(SummaryData.ShowLottable06OnRFReceipt)">
				<span>批属性06<b v-if="parseInt(SummaryData.Lottable06OnRFReceiptMandatory)">*</b></span>
				<input   ref="Lottable06OnRFReceiptMandatory"   @change="toUppercase('Lottable06OnRFReceiptMandatory')"  copyRef="Lottable06OnRFReceiptMandatory"     requireds="Lottable06OnRFReceiptMandatory"  @blur="onblur(parseInt(SummaryData.Lottable06OnRFReceiptMandatory),'Lottable06OnRFReceiptMandatory')"    @focus="g_focus('Lottable06OnRFReceiptMandatory')"   @keyup.enter="nextFocus('Lottable06OnRFReceiptMandatory')"   v-model="Lottable06OnRFReceiptMandatory"></input>
				<i v-if="Lottable06OnRFReceiptMandatory" @click="clearInput('Lottable06OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable07OnRFReceipt)">
				<span>批属性07<b v-if="parseInt(SummaryData.Lottable07OnRFReceiptMandatory)">*</b></span>
				<input   ref="Lottable07OnRFReceiptMandatory"  @change="toUppercase('Lottable07OnRFReceiptMandatory')"  copyRef="Lottable07OnRFReceiptMandatory"   @blur="onblur(parseInt(SummaryData.Lottable07OnRFReceiptMandatory),'Lottable07OnRFReceiptMandatory')"  requireds="Lottable07OnRFReceiptMandatory"   @focus="g_focus('Lottable07OnRFReceiptMandatory')"   @keyup.enter="nextFocus('Lottable07OnRFReceiptMandatory')"    v-model="Lottable07OnRFReceiptMandatory"></input>
				<i v-if="Lottable07OnRFReceiptMandatory" @click="clearInput('Lottable07OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable08OnRFReceipt)">
				<span>批属性08<b v-if="parseInt(SummaryData.Lottable08OnRFReceiptMandatory)">*</b></span>
				<input   ref="Lottable08OnRFReceiptMandatory"   @change="toUppercase('Lottable08OnRFReceiptMandatory')"    copyRef="Lottable08OnRFReceiptMandatory"  @blur="onblur(parseInt(SummaryData.Lottable08OnRFReceiptMandatory),'Lottable08OnRFReceiptMandatory')"  requireds="Lottable08OnRFReceiptMandatory"  @focus="g_focus('Lottable08OnRFReceiptMandatory')"   @keyup.enter="nextFocus('Lottable08OnRFReceiptMandatory')"    v-model="Lottable08OnRFReceiptMandatory"></input>
				<i v-if="Lottable08OnRFReceiptMandatory" @click="clearInput('Lottable08OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable01OnRFReceipt)">
				<span>批属性01<b v-if="parseInt(SummaryData.Lottable01OnRFReceiptMandatory)">*</b></span>
				<input   ref="Lottable01OnRFReceiptMandatory"   @change="toUppercase('Lottable01OnRFReceiptMandatory')"   copyRef="Lottable01OnRFReceiptMandatory"  @blur="onblur(parseInt(SummaryData.Lottable01OnRFReceiptMandatory),'Lottable01OnRFReceiptMandatory')"  requireds="Lottable01OnRFReceiptMandatory"  @focus="g_focus('Lottable01OnRFReceiptMandatory')"  @keyup.enter="nextFocus('Lottable01OnRFReceiptMandatory')"    v-model="Lottable01OnRFReceiptMandatory"></input>
				<i v-if="Lottable01OnRFReceiptMandatory" @click="clearInput('Lottable01OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable09OnRFReceipt)">
				<span>收货日期<b v-if="parseInt(SummaryData.Lottable09OnRFReceiptMandatory)">*</b></span>
				<input   ref="Lottable09OnRFReceiptMandatory"  @change="toUppercase('Lottable09OnRFReceiptMandatory')"   copyRef="Lottable09OnRFReceiptMandatory"   @blur="onblur(parseInt(SummaryData.Lottable09OnRFReceiptMandatory),'Lottable09OnRFReceiptMandatory')"  requireds="Lottable09OnRFReceiptMandatory"   @focus="g_focus('Lottable09OnRFReceiptMandatory')"   @keyup.enter="nextFocus('Lottable09OnRFReceiptMandatory')"    v-model="Lottable09OnRFReceiptMandatory"></input>
				<i v-if="Lottable09OnRFReceiptMandatory" @click="clearInput('Lottable09OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable10OnRFReceipt)">
				<span>批属性10<b v-if="parseInt(SummaryData.Lottable10OnRFReceiptMandatory)">*</b></span>
				<input   ref="Lottable10OnRFReceiptMandatory"     @change="toUppercase('Lottable10OnRFReceiptMandatory')"      copyRef="Lottable10OnRFReceiptMandatory"  @blur="onblur(parseInt(SummaryData.Lottable10OnRFReceiptMandatory),'Lottable10OnRFReceiptMandatory')"  requireds="Lottable10OnRFReceiptMandatory"  @focus="g_focus('Lottable10OnRFReceiptMandatory')"  @keyup.enter="nextFocus('Lottable10OnRFReceiptMandatory')"    v-model="Lottable10OnRFReceiptMandatory"></input>
				<i v-if="Lottable10OnRFReceiptMandatory" @click="clearInput('Lottable10OnRFReceiptMandatory')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>托盘号</span>
				<input   ref="input_PalleNumber"   @change="toUppercase('input_PalleNumber')"    copyRef="input_PalleNumber"  @keyup.enter="getSelect('input_LibraryCode')"  @focus="g_focus('input_PalleNumber')"   v-model="input_PalleNumber"></input>
				<i v-if="input_PalleNumber" @click="clearInput('input_PalleNumber')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>库位代码</span>
				<input  ref="input_LibraryCode"  @change="toUppercase('input_LibraryCode')"   @keyup.enter="submit"   @focus="g_focus('input_LibraryCode')"  v-model="input_LibraryCode"></input>
				<i v-if="input_LibraryCode" @click="clearInput('input_LibraryCode')"  class="iconfont icon-cuowu"></i>
			</div>
			<p class="positionFixed"><span class="g_singleBtn" @click="submit">确定</span></p>
			<div class="kong"></div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"TakeDelivery",
		data(){
			return{
				input_ReceiptNum:'',//收货单号
				input_CustomerNum:'',//客户单号
				input_CommodityCode:'',//商品代码 1010001000
				input_CustomerCode:'',//客户代码
				input_CommodityName:'',//商品名称
				input_toBeReceived:'',//待收数量
				input_AmountReceived:'',//已收数量
				input_num:'',//数量
				input_unit:'',//单位
				input_StateOfGoods:'',//货物状态 代码
				Lottable03OnRFReceiptMandatory:'',//生产批号
				Lottable04OnRFReceiptMandatory:'',//生产日期
				Lottable05OnRFReceiptMandatory:'',//到期日期
				Lottable06OnRFReceiptMandatory:"",//批属性06
				Lottable07OnRFReceiptMandatory:"",//批属性07
				Lottable08OnRFReceiptMandatory:"",//批属性08
				Lottable01OnRFReceiptMandatory:"",//批属性01
				Lottable09OnRFReceiptMandatory:"",//收货日期
				Lottable10OnRFReceiptMandatory:"",//批属性10
				input_PalleNumber:'',//托盘号
				input_LibraryCode:'STAGE',//库位代码
				warningInfo:"",//警告信息
				tipsHide:false,//提示信息是否出现
				msg:"",//提示信息内容
				roterPath:"",//上一页路由名称
				inputFocus:"",
				StateOfGoodsData:[],//货物状态数组
			}
		},
		mounted() {

			var that = this;

			this.$refs['input_num'].focus();

			window.QRScanAjax = this.QRScanAjax;

			$("input").attr("maxlength","50");

			propHide("Lottable01OnRFReceiptMandatory");
			propHide("Lottable03OnRFReceiptMandatory");
			propHide("Lottable04OnRFReceiptMandatory");
			propHide("Lottable05OnRFReceiptMandatory");
			propHide("Lottable06OnRFReceiptMandatory");
			propHide("Lottable07OnRFReceiptMandatory");
			propHide("Lottable08OnRFReceiptMandatory");
			propHide("Lottable09OnRFReceiptMandatory");
			propHide("Lottable10OnRFReceiptMandatory");
			
			function propHide(propName){
				
				if(!that.$refs[propName]){

					that[propName] = "";
				}else{

					if(propName == "Lottable04OnRFReceiptMandatory" || propName == "Lottable05OnRFReceiptMandatory" ){

						that[propName] = that[propName].substring(0,10);
					}
				}
			}
			$("input[requireds]").each(function(i){

				if(parseInt(that.SummaryData[$("input[requireds]").eq(i).attr("requireds")])){
					

					if(!$(this).val()){
						$("input[requireds]").eq(i).css("border-color","red");
					
					}
				}
			})

		}, 
		created(){

			var that = this;

			// 收货单号等数据
			if(that.$store.state.Receipt_SubmitData.length){

				var Receipt_SubmitData = that.$store.state.Receipt_SubmitData;
				
				that.input_ReceiptNum = Receipt_SubmitData[0].receiptkey;

				that.input_CustomerNum = Receipt_SubmitData[0].externalreceiptkey2;

				that.input_CustomerCode = Receipt_SubmitData[0].storerkey;
			}

			// 页面显示商品代码等数据  批属性是否显示数据
			if(that.$store.state.RFReceiveSkuData.length){
				
				var SummaryData = that.$store.state.RFReceiveSkuData[0];

				that.input_CommodityName = SummaryData.Descr; //商品名称

				that.input_AmountReceived = SummaryData.qtyReceived; //已收数量

				that.input_toBeReceived = SummaryData.qtyRemain; //待收数量

				that.input_CommodityCode = SummaryData.Sku; //商品代码

				that.input_unit = SummaryData.Susr2; //单位

				this.SummaryData = SummaryData;

				this.SummaryData.input_num =1;
			}

 			// 上一次批属性值
			if(that.$store.state.Receipt_PrevBatchProp.length){

				var PrevBatchProp = that.$store.state.Receipt_PrevBatchProp;

				that.Lottable01OnRFReceiptMandatory = PrevBatchProp[0].Lottable01;
				that.input_StateOfGoods = PrevBatchProp[0].Lottable02;
				that.Lottable03OnRFReceiptMandatory = PrevBatchProp[0].Lottable03;

				that.Lottable04OnRFReceiptMandatory = PrevBatchProp[0].Lottable04;
				that.Lottable05OnRFReceiptMandatory = PrevBatchProp[0].Lottable05;

				// if(PrevBatchProp[0].Lottable04.length){
				// 	that.Lottable04OnRFReceiptMandatory = PrevBatchProp[0].Lottable04;
				// }
				that.Lottable06OnRFReceiptMandatory = PrevBatchProp[0].Lottable06;
				that.Lottable07OnRFReceiptMandatory = PrevBatchProp[0].Lottable07;
				that.Lottable08OnRFReceiptMandatory = PrevBatchProp[0].Lottable08;
				that.Lottable09OnRFReceiptMandatory = PrevBatchProp[0].Lottable09;
				that.Lottable10OnRFReceiptMandatory = PrevBatchProp[0].Lottable10;
			}

			if(that.$store.state.StateOfGoodsData.length){

				that.StateOfGoodsData = that.$store.state.StateOfGoodsData;

				console.log(that.input_StateOfGoods)

				for(var m = 0; m < that.StateOfGoodsData.length ; m++){

					if(that.input_StateOfGoods == that.StateOfGoodsData[m].Code){

						console.log(that.input_StateOfGoods)
						console.log(that.StateOfGoodsData[m].Code)

						setInterval(function(){
							$("select>option").eq(m).attr("selected","selected").siblings().removeAttr("selected");
						},20)

						break;
					}
				}

			}else{
				var stateUrl = this.HOST + "GoodsStatus.do";

				that.httpRequest(stateUrl,{Code:"",Descr:""},function(res){

					that.StateOfGoodsData = res.data.data;

					console.log(that.input_StateOfGoods)

					that.$store.state.StateOfGoodsData = res.data.data;

					for(var mn = 0; mn< that.StateOfGoodsData.length ; mn++){

						if(that.input_StateOfGoods == that.StateOfGoodsData[mn].Code){
	
							 setInterval(function(){
								$("select>option").eq(mn).attr("selected","selected").siblings().removeAttr("selected");
							},20)
							break;
						}
					}
				})
			}
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;
				
				if(inputName == "input_PalleNumber") {

					this.getSelect('input_LibraryCode');

				// 库位代码
				}else if(inputName == "input_LibraryCode") {

					this.submit();
				}
			},
			// 返回上一页
			ToReceipt:function(){

				var that = this;

				that.$store.state.Receipt_PrevBatchProp = [];

				this.$router.push({
					name:"ScanCommodityCode",
					query:{
						input_CommodityCode:that.input_CommodityCode,//商品代码
					}
				})
			},
			// 失去焦点
			onblur:function(index,attributeName){
				// this.$refs['input_num'].onkeydown();

				if(index){

					$("input[requireds]").each(function(i){

						if($("input[requireds]").eq(i).attr("requireds") == attributeName){

							if($("input[requireds]").eq(i).val()){

								$("input[requireds]").eq(i).css("border-color","#bbb");

							}else{

								$("input[requireds]").eq(i).css("border-color","red");
							}
						}
					})
				}
			},
			// 回车时 光标调到下一个必填项
			nextFocus:function(thisName){

				var that = this;

				var notFocus = true;
				var thisIdx;
				$("input[requireds]").each(function(i){

					console.log(i)

					if(parseInt(that.SummaryData[$("input[requireds]").eq(i).attr("requireds")])){

						if(!$(this).val()){
							$("input[requireds]").eq(i).css("border-color","red");
						}
					}

					if(thisName == $("input[requireds]").eq(i).attr("requireds")){

						thisIdx = i + 1;
					}

					if(parseInt(that.SummaryData[$("input[requireds]").eq(thisIdx).attr("requireds")])){

						that.$refs[$("input[requireds]").eq(thisIdx).attr("copyRef")].focus();

						notFocus = false;
	
						return;

					}else{

						thisIdx += 1;
					}

				})
				
				if(notFocus){
					that.$refs["input_PalleNumber"].focus();
				}

			},
			// 收货确认 提交
			submit:function(){

				var that = this;

				var banSubmit = false;

				$("input[requireds]").each(function(i){

					if(parseInt(that.SummaryData[$("input[requireds]").eq(i).attr("requireds")])){
						

						if(!$(this).val()){
							$("input[requireds]").eq(i).css("border-color","red");
							banSubmit = true;
						}
					}
				})

				// 如果有必填项 未填，则return
				if(banSubmit){
					
					var isInputFocus = false;

					$("input[requireds]").each(function(i){

						if(isInputFocus){
							return;
						}

						if($("input[requireds]").eq(i).css("border-color") == "rgb(255, 0, 0)"){

							that.inputFocus =  $("input[requireds]").eq(i).attr("requireds");

							isInputFocus = true;

							return;
						}
					})

					that.tipsShow("input_LibraryCode");

					that.msg = "请填写必填项";

					return;
				}

				// 页面显示 货物状态
				if(that.$refs['input_StateOfGoods']){
					// 货物状态代码
					for(var j = 0;j < that.StateOfGoodsData.length ; j++){

						if(that.$refs['input_StateOfGoods'].value == that.StateOfGoodsData[j].Descr){

							that.input_StateOfGoods = that.StateOfGoodsData[j].Code;
						}
					}
				}else{
					that.input_StateOfGoods = that.StateOfGoodsData[0].Code;
				}
				

				// 生产日期
				if(that.Lottable04OnRFReceiptMandatory){

					that.Lottable04OnRFReceiptMandatory = that.ValidationTimeFormat(that.Lottable04OnRFReceiptMandatory,"Lottable04OnRFReceiptMandatory");

					var Date_year = new Date().getFullYear();
					var Date_month = new Date().getMonth();
					var Date_day = new Date().getDate();

					var productDate = that.Lottable04OnRFReceiptMandatory;

					// 当生产日期大于当前日期 
					if( parseInt(productDate.substring(0,4)) > parseInt(Date_year)){

						that.tipsShow("input_LibraryCode");

						that.msg = "生产日期不能大于当前日期";

						that.inputFocus = "Lottable04OnRFReceiptMandatory";

						return;

					}else if( parseInt(productDate.substring(0,4)) == parseInt(Date_year) && parseInt(productDate.substring(5,7)) > (parseInt(Date_month) + 1)){

						that.tipsShow("input_LibraryCode");

						that.msg = "生产日期不能大于当前日期";

						that.inputFocus = "Lottable04OnRFReceiptMandatory";

						return;

					}else if(parseInt(productDate.substring(0,4)) == parseInt(Date_year) && parseInt(productDate.substring(5,7)) == (parseInt(Date_month) + 1) && parseInt(productDate.substring(8,10)) > parseInt(Date_day)){

						that.tipsShow("input_LibraryCode");

						that.msg = "生产日期不能大于当前日期";

						that.inputFocus = "Lottable04OnRFReceiptMandatory";

						return;
					}

					if(!that.Lottable04OnRFReceiptMandatory){

						that.getFocus("Lottable04OnRFReceiptMandatory");
						return;
					}
				}

				// 到期日期
				if(that.Lottable05OnRFReceiptMandatory){

					that.Lottable05OnRFReceiptMandatory = that.ValidationTimeFormat(that.Lottable05OnRFReceiptMandatory,"Lottable05OnRFReceiptMandatory");

					if(!that.Lottable05OnRFReceiptMandatory){

						that.getFocus("Lottable05OnRFReceiptMandatory");
						return
					}
				}

				// 收货日期
				if(that.Lottable09OnRFReceiptMandatory){
					that.Lottable09OnRFReceiptMandatory = that.ValidationTimeFormat(that.Lottable09OnRFReceiptMandatory,"Lottable09OnRFReceiptMandatory");

					if(!that.Lottable09OnRFReceiptMandatory){

						that.getFocus("Lottable09OnRFReceiptMandatory");
						return
					}
				}
				
				var RFReceiveUrl = this.HOST + "RFReceive.do";

				var r_postData = {
					ReceiptKey : this.input_ReceiptNum,
					StorerKey : this.input_CustomerCode,
					Sku : this.input_CommodityCode,
					Qty : this.input_num,
					Lottable03 : this.Lottable03OnRFReceiptMandatory,
					Lottable02 : this.input_StateOfGoods,
					Lottable04 : this.Lottable04OnRFReceiptMandatory,
					Lottable05 : this.Lottable05OnRFReceiptMandatory,
					Lottable06 : this.Lottable06OnRFReceiptMandatory,
					Lottable07 : this.Lottable07OnRFReceiptMandatory,
					Lottable08 : this.Lottable08OnRFReceiptMandatory,
					Lottable09 : this.Lottable09OnRFReceiptMandatory,
					Lottable10 : this.Lottable10OnRFReceiptMandatory,
					Lottable01 : this.Lottable01OnRFReceiptMandatory,
					PID : this.input_PalleNumber,
					Loc : this.input_LibraryCode,
				}


				this.httpRequest(RFReceiveUrl,r_postData,function(res){

					that.$store.state.Receipt_PrevBatchProp = [];

					that.$router.push({
						name:"ScanCommodityCode",
						query:{
						}
					})
				},)
			},
			//处理时间格式
			ValidationTimeFormat:function(time,focusName){

				var that = this;

				var year = new Date().getFullYear() + '';

				if(time.length==4 || time.length==6 || time.length==8 || time.length==10 ){

					if(time.length==10){

						if(time[4]=="-" && time[7]=="-"){

							return time;

						}else{

							that.tipsShow("input_LibraryCode");

							that.msg = "请填写正确的日期格式 ; 例如20180524 , 或2018-05-24";

							that.inputFocus = focusName;

							return;
						}
					}else if(time.length==4){

						return year + "-" + time.substring(0,2) + "-" + time.substring(2,4);

					}else if(time.length==6 ){

						return year.substring(0,2) +  time.substring(0,2) + "-" + time.substring(2,4) + "-" + time.substring(4,6);

					}else if(time.length==8 ){

						return  time.substring(0,4) + "-" + time.substring(4,6) + "-" + time.substring(6,8);

					}

				}else{

					that.tipsShow("input_LibraryCode");

					that.msg = "请填写正确的日期格式 ; 例如20180524 , 或2018-05-24";

					that.inputFocus = focusName;

					return;
				}
			}
		}
	}
</script>
