<template>
	<div  class="Login">
		<header><span>登录</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="loginContainer">
				<div>
					<i class="iconfont icon-zuhu"></i>
					<input class='userName'  ref="Tenant_Code"  @focus="g_focus('Tenant_Code')"   style="text-transform:none;"   @keyup.enter="getFocus('userName')"  v-model="Tenant_Code"  placeholder='请输入租户代码' ></input>
					<i v-if="Tenant_Code" @click="clearInput('Tenant_Code')" class="iconfont icon-cuowu"></i>
				</div>
				<div>
					<i class="iconfont icon-yonghuming"></i>
					<input class='userName'  ref="userName"  @focus="g_focus('userName')"   style="text-transform:none;"   @keyup.enter="getFocus('password')"  v-model="userName"  placeholder='请输入账号' ></input>
					<i v-if="userName" @click="clearInput('userName')" class="iconfont icon-cuowu"></i>
				</div>
				<div>
				 	<i class="iconfont icon-lock" ></i>
				 	<input class='pswd' type='password'  ref="password"  @focus="g_focus('password')"   style="text-transform:none;"  v-model="password"  @keyup.enter="login"  placeholder='请输入密码' ></input>
				 	<i v-if="password" @click="clearInput('password')" class="iconfont icon-cuowu"></i>
				</div>
			  <button class='login' @click='login'>登录</button>
			</div>
			<div class="ThirdPartyLogin" ref="ThirdPartyLogin">
				<div class="ThirdParty_title">
					<div></div>
					<div>第三方登录</div>
					<div></div>
				</div>
				<div class="ThirdParty_content" @click="ThirdPartyLogin"><i class="iconfont icon-weixin"></i></div>
			</div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  id="g_tips"   class="g_tips">
			<div  v-if="!server_err">
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p   @click='tipsHideen(inputFocus)'>确定</p>
			</div>
			<div  v-else  class="serverErr">
				<span>提示信息</span>
				<span>请求失败,是否重置服务器地址</span>
				<div><span @click="server_reset">重置</span><span  @click="server_cancer">取消</span></div>
			</div>
		</div>

		<div class="SelectMeng"   v-if="selectMShow">

			<div class="SelectWarehouses" >
				<div class="SelectTitle">请选择仓库</div>
				<div class="SelectItem">
					<div v-for='(dataItem,index) in WarehousesList'  :id="index"  :key='index'  @click="SelectComit(index)">{{dataItem.WarehouseName}}</div>
				</div>
				<div class="cancel"   @click="selectCancel">取消</div>
			</div>
			
		</div>
		<div class="verseion">{{this.$store.state.App_Version}}</div>
	</div>
</template>
<script type="text/javascript">
	// 使用MD5对密码加密
	import md5 from 'js-md5';

	export default {
		name:"Login",
		data(){
			return{
				userName:"",//用户名
				password:"",//密码
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",//遮罩层点击确认后 input聚焦
				WarehousesList:[],//仓库列表
				selectMShow:false,//选择仓库蒙版是否出现
				Tenant_Code:"",//租户代码
				server_err:false,//提示服务器地址是否重置 弹出框
			}
		},
		mounted() {
			this.$refs['Tenant_Code'].focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
			window.WXInstall_Check_Ajax = this.WXInstall_Check_Ajax;
			window.WXBind_NO_Ajax = this.WXBind_NO_Ajax;
			window.WXBind_YES_Ajax = this.WXBind_YES_Ajax;
			window.VersionShow = this.VersionShow;
			window.SetUserNameAndPassword = this.SetUserNameAndPassword;
			this.loginVueMounted();
			window.SetServerAddressDefault = this.SetServerAddressDefault;
		},
		created(){

			// 仓库
			this.$store.state.WarehouseCode = "";

			// 用户名
			this.$store.state.UserID = "";

			// 权限列表
			this.$store.state.limitControlList = [];

			// 租户代码
			this.$store.state.Tenant_Code = [];

			// 获取 用户名 密码 租户代码cookie
			this.getCookie("e");

			// 获取服务器地址cookie
			this.getCookie_Server();
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;
				
				if(inputName == "userName") {

					this.getFocus('password');
					
				}else if(inputName == "password") {

					this.login();
				}
			},
			// 检查手机是否安装微信，以免App Store审核被拒
			WXInstall_Check_Ajax:function(isInstall) {

				if(isInstall == "NO") {

					this.$refs['ThirdPartyLogin'].remove();
				}
			},
			// 未关联微信，跳转关联界面
			WXBind_NO_Ajax:function(openid) {

				this.$store.state.Bind_Openid = openid;
				this.$router.push({
					name:"Binding",
					query:{
					}
				})
			},
			// 已关联微信，登录成功
			WXBind_YES_Ajax:function(data) {

				// 填充帐号
				var str = decodeURIComponent(data);
				var obj = JSON.parse(str);
				this.userName = obj.data[0].UserID;
				this.$store.state.UserID = obj.data[0].UserID;
		
				try {
					this.$store.state.Tenant_Code = obj.data[0].Tenant_Code;
				}
				catch(error) {
				}

				// 弹出仓库选择
				this.selectMShow = true;
				this.WarehousesList = obj.data;
			},
			// 显示版本号
			VersionShow:function(v) {

				this.$store.state.App_Version = v;
			},
			SetUserNameAndPassword:function(u, p, t) {

				this.userName = u;
				this.password = p;
				this.Tenant_Code = t;
			},
			SetServerAddressDefault:function(a) {

				this.$store.state.Server_Address  = a;
			},
			// 重置服务器地址
			server_reset(){

				this.$store.state.Server_Address = "https://scm.cy-scm.com/wms/";

				//设置cookie
				this.setCookie_Server("https://scm.cy-scm.com/wms/");
				this.KeepServerAddressDefault("https://scm.cy-scm.com/wms/");

				this.tipsHide = false ;
			},
			// 不重置服务器地址
			server_cancer(){

				this.tipsHide = false ;
			},
			// 点击登录按钮
			login:function(){

				var that = this;

				that.server_err = false;

				this.$refs["password"].blur();

				if(!this.Tenant_Code) {

					that.inputFocus = "userName";
					that.tipsShow("password");
					that.msg = "租户代码不能为空";
					
					return;
				}
				
				if(!this.userName){

					that.inputFocus = "userName";
					that.tipsShow("password");
					that.msg = "用户名不能为空";
					
					return;

				}else if(!this.password){

					that.inputFocus = "password";
					that.tipsShow("password");
					that.msg = "密码不能为空";
					
					return;
				}

				function encodeUtf8(text) {

					const code = encodeURIComponent(text);

				    const bytes = [];

				    for (var i = 0; i < code.length; i++) {

				        const c = code.charAt(i);

				        if (c === '%') {

				            const hex = code.charAt(i + 1) + code.charAt(i + 2);

				            const hexVal = parseInt(hex, 16);

				            console.log(hexVal)

				            bytes.push(hexVal);

				            i += 2;

				        } else {

				        	bytes.push(c.charCodeAt(0));
				        }
				    }
				    return bytes;
				}
			
				var EncryptArray = encodeUtf8(this.password);

			    var  Encrypt =  md5.array(EncryptArray);

	            var str = [],bt;

				var  j = 0;

				var keyMap =  "C2E8D9A3B5F14607".split("");

				for (var i = 0; i < 16; i++) {

					bt = Encrypt[i];

					str[j++] = keyMap[bt >>> 4 & 0xf];

					str[j++] = keyMap[bt & 0xf];
				}

				var resurtData = str.join('');

				var currentUrl = this.HOST + "RFLogin.do";

				var postData = {
					"UserID":this.userName,
					"Password":resurtData,
					"Tenant_Code":this.Tenant_Code,
				}

				this.httpRequest(currentUrl,postData,function(res){

					that.selectMShow = true;

					that.WarehousesList = res.data.data;

					that.KeepUserNameAndPassord(that.userName, that.password, that.Tenant_Code);
						
				},"userName")
			},
			//选择具体仓库
			SelectComit:function(index){

				var that = this;

				this.$store.state.WarehouseCode = this.WarehousesList[index].WarehouseCode;//将所选仓库保存到全局变量
				this.$store.state.UserID = this.userName; //将用户名保存到全局变量
				this.$store.state.Tenant_Code = this.Tenant_Code; //将租户代码保存到全局变量


				this.selectMShow = false;

				this.setCookie(that.userName,that.password,that.Tenant_Code,1/24);

				//跳转到首页
				this.$router.push({
					name:"Index",
					query:{
					}
				})
			},
			// 取消选择仓库
			selectCancel:function(){

				this.selectMShow = false;

			},
			// 第三方登录
			ThirdPartyLogin:function(){

				// 安卓
				try {

					CallAndroidOrIOS.callAndroid("微信登录","");

				} 
				catch(error) {

					//console.log("没有CallAndroidOrIOS.callAndroid方法")
				}

				// 苹果
				try {

						CallAndroidOrIOS("微信登录");

				}
				catch(error) {

						//console.log("没有CallAndroidOrIOS方法")
				}
			}
		}
	}
</script>
<style lang="less" scoped>

	.loginContainer{
		width:638/50rem;
		margin:150/50rem auto 0;
		&>div{
			width:638/50rem;
		    height:70/50rem;
		    position: relative;
		    margin-bottom: 50/50rem;
		    i.icon-yonghuming,i.icon-lock,i.icon-zuhu{
		    	position: absolute;
		    	top:15/50rem;
		    	left:20/50rem;
		    	color: #265AC3;
				font-size: 40/50rem;
				font-weight: 500;
		    }
		    i.icon-cuowu{
				position:absolute;
				right:20/50rem;
				font-size: 30/50rem;
				top: 20/50rem;
			}
		}
	    .userName,.pswd{
		    width:638/50rem;
		    height:70/50rem;
		    border-radius: 10/50rem;
		    padding-left: 82/50rem;
		    font-size: 26/50rem;
		    box-sizing: border-box;
		}
		.login{
		    width:640/50rem;
		    height:73/50rem;
		    line-height: 73/50rem;
		    font-size: 30/50rem;
		    background-color:#285DCE;
		    color: #fff;
		    margin-top: 50/50rem;
		    border:none;
		    border-radius: 10/50rem;
		    outline: none;
		}
		
	}
	.SelectMeng{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		background-color: rgba(0,0,0,0.3);
		z-index: 999;
		.SelectWarehouses{
			width: 500/50rem;
			position: absolute;
			left: 125/50rem;
			top:50%;
			margin-top: -340/50rem;
			background-color: #F1F0F1;
			text-align: center;
			border-radius: 10/50rem;
			&>div{
				width: 100%;
			}
			.SelectItem{
				color: #2F84F0;
				font-size: 29/50rem;
				overflow: scroll;
				max-height: 486/50rem;
				&>div{
					height: 80/50rem;
					line-height: 80/50rem;
					border-top: 1/50rem solid #D9D7D9;
				}
			}
			.SelectTitle{
				height: 114/50rem;
				line-height: 114/50rem;
				font-weight: 600;
				font-size: 34/50rem;
			}
			.cancel{
				height: 80/50rem;
				line-height: 80/50rem;
				color: #2F84F0;
				font-size: 34/50rem;
				font-weight: 600;
				border-top:  1/50rem solid #D9D7D9;
			}
		}
	}
	.ThirdPartyLogin{
		width: 100%;
		overflow: hidden;
		margin-top: 290/50rem;
		.ThirdParty_title{
			margin: 0 auto;
			height: 50/50rem;
			&>div{
				float: left;
				height: 50/50rem;
				font-size:30/50rem;
				color: #265AC3;
			}
			&>div:nth-child(1),&>div:nth-child(3){
				height: 3/50rem;
				margin-top: 22.5/50rem;
				width:247.5/50rem;
				background:-webkit-gradient(linear, left top, right top, from(#fff), to(#265AC3));
			}
			&>div:nth-child(3){
				background:-webkit-gradient(linear, right top, left top, from(#fff), to(#265AC3));
			}
			&>div:nth-child(2){
				width: 255/50rem;
				text-align: center;
			}
		}
		.ThirdParty_content{
			width: 80/50rem;
			height: 80/50rem;
			border-radius: 50%;
			border:1/50rem solid #469E3D;
			color:#469E3D;
			margin: 30/50rem auto 30/50rem;
			text-align: center;
			line-height: 73/50rem;
			i{
				font-size: 45/50rem;
			}
		}
	}

	@media screen  and (max-width:340px){
		.loginContainer{
			&>div{
			    height:80/50rem;
			    i.icon-cuowu{
					font-size: 40/50rem;
					top: 20/50rem;
				}
			}
		    .userName,.pswd{
			    height:80/50rem;
			    font-size: 35/50rem;
			}
			.login{
			    height:80/50rem;
			    line-height: 80/50rem;
			    font-size: 37/50rem;
			    margin-top: 35/50rem;
			}
			
		}
		.SelectMeng{
			.SelectWarehouses{
				.SelectItem{
					font-size: 35/50rem;
				}
				.SelectTitle{
					font-weight: 500;
					font-size: 43/50rem;
				}
				.cancel{
					font-size: 40/50rem;
					font-weight: 500;
				}
			}
		}
		.ThirdPartyLogin{
			margin-top: 40/50rem;
			.ThirdParty_title{
				&>div{
					font-size:37/50rem;
				}
				&>div:nth-child(1),&>div:nth-child(3){
					height: 5/50rem;
				}
			}
			.ThirdParty_content{
				width: 95/50rem;
				height: 95/50rem;
				border-radius: 50%;
				border:3/50rem solid #469E3D;
				margin: 15/50rem auto 0;
				line-height: 89/50rem;
				i{
					font-size: 60/50rem;
				}
			}
		}
	}
	@media screen  and (max-width:289px){
		.loginContainer{
			&>div{
			    height:95/50rem;
			     i.icon-yonghuming,i.icon-lock,i.icon-zuhu{
					font-size: 55/50rem;
			    }
			    i.icon-cuowu{
					font-size: 50/50rem;
					top: 22/50rem;
				}
			}
		    .userName,.pswd{
			    height:95/50rem;
			    font-size: 50/50rem;
			}
			.login{
			    height:100/50rem;
			    line-height: 80/50rem;
			    font-size: 48/50rem;
			    margin-top: 35/50rem;
			}
			
		}
		.SelectMeng{
			.SelectWarehouses{
				width: 600/50rem;
				left: 75/50rem;
				.SelectItem{
					font-size: 45/50rem;
					max-height: 480/50rem;
					&>div{
						height: 95/50rem;
						line-height: 95/50rem;
					}
				}
				.SelectTitle{
					height: 114/50rem;
					line-height: 114/50rem;
					font-size: 50/50rem;
				}
				.cancel{
					height: 90/50rem;
					line-height: 90/50rem;
					font-size: 48/50rem;
				}
			}
		}
		.ThirdPartyLogin{
			margin-top: 40/50rem;
			.ThirdParty_title{
				&>div{
					font-size:45/50rem;
				}
			}
			.ThirdParty_content{
				margin: 30/50rem auto 0;
			}
		}
	}

	@media screen  and (max-width:1020px) and (min-width:341px){
		.ThirdPartyLogin{
			margin-top: 370/50rem;
		}
	}

	@media screen  and (min-width:1021px){
		.ThirdPartyLogin{
			margin-top: 50/50rem;
		}
	}
	@media screen and (max-width: 374px) {	
		.SelectMeng{
			.SelectWarehouses{
				.SelectItem{
					&>div{
						border-top: 3/50rem solid #D9D7D9;
					}
				}
				.cancel{
					border-top:  3/50rem solid #D9D7D9;
				}
			}
		}
	}

	.verseion{
		width:200/50rem;
		height: 40/50rem;
		color: #ccc;
		font-size: 29/50rem;
		text-align: center;
		position: fixed;
		bottom: 0;
		right: 0;
	}
</style>


