<template>
	<div class="Summary">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>{{title}}</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td v-if="ifSelect">选择</td>
						<td  @click="SortArr('Receipt_SummaryData','sku','SortCondition1',$event)"  class="goodsCode">商品代码</td>
						<td  @click="SortArr('Receipt_SummaryData','qtyremain','SortCondition2',$event)">待收数量</td>
						<td  @click="SortArr('Receipt_SummaryData','qtyexpected','SortCondition3',$event)">预期数量</td>
						<td  @click="SortArr('Receipt_SummaryData','qtyreceived','SortCondition4',$event)">已收数量</td>
						<td  @click="SortArr('Receipt_SummaryData','descr','SortCondition5',$event)" class="goodsName">商品名称</td>
						<td  @click="SortArr('Receipt_SummaryData','susr2','SortCondition6',$event)">单位</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in Receipt_SummaryData'  :id="index"  :key='index'   @click="selectData(index)">
						<td v-if="ifSelect" class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.sku}}</td>
						<td>{{dataItem.qtyremain}}</td>
						<td>{{dataItem.qtyexpected}}</td>
						<td>{{dataItem.qtyreceived}}</td>
						<td>{{dataItem.descr}}</td>
						<td>{{dataItem.susr2}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
			<p class="positionFixed"  v-if="ifSelect"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"Summary",
		data(){
			return{
				Receipt_SummaryData:[],//列表数据
				title:"收货汇总",
				detail_item:0,//默认为第一个(选择商品)
				ifSelect:false,//是否显示选择项
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
				SortCondition6:false,
			}
		},
		mounted(){
			if(this.ifSelect){

				$("thead").find("td").eq(0).siblings().append('<i class="iconfont"></i>')
			}else{

				$("thead").find("td").append('<i class="iconfont"></i>')
			}
			
		},
		created:function(){

			this.title = this.$route.query.title;

			this.ifSelect = this.$route.query.ifSelect;


			if(this.$store.state.Receipt_SummaryData){

				var Receipt_SummaryData = this.$store.state.Receipt_SummaryData;

				this.Receipt_SummaryData = Receipt_SummaryData;
			}
		},
		methods:{
			//返回上一页
			goPrev:function(){

				var that = this;

				// 录入单号 页面跳转而来
				if(this.$route.query.Receipt){
					this.$router.push({
						name:"Receipt",
						query:{
							ReceiptKey:that.$route.query.Receipt.ReceiptKey,
							ExternalReceiptKey2: that.$route.query.Receipt.ExternalReceiptKey2
						}
					})
				// 扫描商品条码 页面跳转而来
				}else if(this.$route.query.TakeDelivery){
					this.$router.push({
						name:"ScanCommodityCode",
						query:{
							ExternalReceiptKey2: that.$route.query.TakeDelivery.ExternalReceiptKey2
						}
					})
				}
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;
			},
			// 点击确认按钮 跳转到 收货页面
			selectData_submit:function(){

				var that = this;

				// 获取最近的一个收货批次
				var LottableUrl = this.HOST + "RFReceiptSkuLottable.do";

				var Lottable_Data = {
					ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,//收货单号
					UserID : that.$store.state.UserID,//用户名
					Sku : that.Receipt_SummaryData[that.detail_item].sku//商品代码
				}

				this.httpRequest(LottableUrl,Lottable_Data,function(resData){
					console.log(resData)
					if(resData.data.data.length){
							
						that.$store.state.Receipt_PrevBatchProp = resData.data.data;
						
					}else{
						that.$store.state.Receipt_PrevBatchProp = [];
					}

					var SummaryUrl = that.HOST + "RFReceiveSku.do";

					var sum_Data = {
						ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,//收货单号
						StorerKey : that.$store.state.Receipt_SubmitData[0].storerkey,//客户代码
						Sku : that.Receipt_SummaryData[that.detail_item].sku//商品代码
					}

					that.httpRequest(SummaryUrl,sum_Data,function(res){

						if(res.data.data.length){
								
							that.$store.state.RFReceiveSkuData = res.data.data;

							that.$router.push({
								name:"TakeDelivery",
								query:{
									Sku : that.Receipt_SummaryData[that.detail_item].sku//商品代码
								}
							})
							
						}else{
							that.tipsShow();

							that.msg = "商品在收货单中不存在";
						}
					})
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.Summary{
		&>.container{
			width:100%;
			padding-bottom: 130/50rem;
			table{
				tr{
					td:nth-child(2),td:nth-child(3),td:nth-child(4),td:nth-child(5),td.goodsCode{
						min-width: 160/50rem;
					}
					td.goodsName{
						min-width: 340/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}
	@media screen and (max-width: 332px) and (min-width: 251px){
		.Summary{
			&>.container{
				table{
					tr{
						td:nth-child(2),td:nth-child(3),td:nth-child(4),td:nth-child(5),td.goodsCode{
							min-width: 200/50rem;
						}
						td.goodsName{
							min-width: 400/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.Summary{
			&>.container{
				table{
					tr{
						td:nth-child(2),td:nth-child(3),td:nth-child(4),td:nth-child(5),td.goodsCode{
							min-width: 260/50rem;
						}
						td.goodsName{
							min-width: 450/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


